body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(img/total_back.jpg);
  background-size: 120%;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Estilos para la cabecera */
h1 {
  color: black;
  font-size: 35px;
  text-align: center;
  font-weight: bold;

  height: 80px;
}
.header {
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
  padding: 0%; /* Ajusta el espaciado seg√∫n tus preferencias */
}

.logo-container {
  
  align-items: center;
}

.logo {
  width: 80%;
  height: auto;
  justify-content: left;
}
.register{
  font-size: 50px;
  font-weight: bold;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  align-items: center;
  justify-content: center;

}

/* Estilos para el cuerpo */

.contenido{
  display: flex;
  justify-content: space-between;
  background-color: rgb(7, 4, 4);
  margin: 3%;
  margin-bottom: 0%;


}
.formulario{
  margin-left: 8%;
  margin-top: 5%;
  margin-bottom: 3%;
  width: 60%;
  margin-right: 3%;
}
form {
  font-size: 25px;
 
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
 
}

label {
  color: rgb(255, 255, 255);
   width: 250%;
}

input[type="text"],
input[type="email"],
input[type="tel"],
select {
  margin-top: 5px;
  margin-bottom: 30px;
  width: 70%;
  height: 35px;
  background-color: rgba(247, 231, 231, 0.771);
  color: black;
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  border: cadetblue;
 
}
input[type="checkbox"],
input[type="radio"]{

  width: 25px;
  height: 25px;
  border: 2px solid #efdada;
  margin-bottom: 9%;
  margin-top: 0.5%;
}
button{
  background-color: rgb(243, 243, 255);
  font-size: 90%;
  margin-top: 30px;
  margin-bottom: 5%;
  color: black;
  width: 50%;
  height: 60px;
  border-radius: 4px;
  border-color: rgb(42, 42, 51);
}

button:hover{
  background-color:  rgb(200, 200, 209);;

  
}
.fotos{
 margin-left: 2%;
 margin-right: 3%;
}
.conferencia{
  width: 98%;
  height: 32%;
  margin-top: 8%;
  margin-left: 1%;
 margin-right: 0%;
}

